@import url(https://fonts.googleapis.com/css2?family=Fira+Mono&display=swap);
*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding:0
}
body {
  font-family: Fira Mono, monospace;
  background: url(/country-quiz/static/media/background.41dbc550.png) no-repeat center / cover;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 1rem;
  color: black;
}
#root{
  width: 100%;
}

img{
  width: 100%;
  display: block;
}

#app{

  max-width: 600px;
  margin: 0 auto 7rem auto;

}
#app h1{
  
  font-size: 2.5rem;
  padding-right: 8rem;
  margin: .5rem auto;
  text-transform: uppercase;
  color: white;
}
#app .container{
  
  margin: 0 auto;
  background: #FFFFFF;
  border-radius: 24px;
  position: relative;
  padding: 4rem 2rem;
}
#app .logo{
  position: absolute;
  width: 7rem;
  height: 5rem;
  right: 0;
  top: -3rem;
  
}
#app .question{

  font-size: 1.5rem;
  line-height: 36px;
  color: #2F527B;
}
#app ul{
  list-style: none;
}

#app li{
  border: 2px solid rgba(96, 102, 208, 0.7);
  color: rgba(96, 102, 208, 0.7);
  margin: 1.7rem 0;
  padding: .5rem 1rem;
  border-radius: 12px;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: .3s;
}

#app li.correct{
  background: #60BF88;
  border-color: #60BF88;;
}
#app li.incorrect{
  background: #EA8282;
  border-color: #EA8282;
}
#app li span{
  display: inline-block;
  
}
#app li span:first-child{
  margin-right: 2rem;
  font-size: 1.5rem;
}
#app li .icon{
  width: 2rem;
  flex: none;
  margin-left: auto;
}
